:root {
  --font-name: "SUIT", "Microsoft Yahei", sans-serif;
  --font-size: 1.6rem;
  --font-weight: 400;
  --font-color: #000000;
  --line-height: 24px;
  --max-width: 1660px;
  --sm: 576px;
  --md: 768px;
  --lg: 1000px;
  --xl: 1440px;
  --xxl: 1660px;
  --gap: 1.5rem;
  --gap-md: 1.5rem;
  --gap-sm: 1.5rem;
  --gutter: max(7.8vw, 13rem);
  --header: 10rem;
  --lg-header: 5rem;
}

* {
  box-sizing: border-box;
}

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, textarea, p, blockquote, th, td, input, select, button {
  margin: 0;
  padding: 0;
}

fieldset, img {
  border: 0 none;
}

dl, ul, ol, menu, li {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

input, select, textarea, button {
  vertical-align: middle;
}

input::-ms-clear {
  display: none;
}

button {
  border: 0 none;
  background-color: transparent;
  cursor: pointer;
}

body, th, td, input, select, textarea, button {
  color: #333;
  font-family: var(--font-name);
}

a {
  color: #333;
  text-decoration: none;
  cursor: pointer;
}

a:active, a:hover {
  text-decoration: none;
}

a:active {
  background-color: transparent;
}

address, caption, cite, code, dfn, em, var {
  font-style: normal;
  font-weight: 400;
}

html {
  font-size: 10px;
}

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, textarea, p, blockquote, th, td, input, select, button {
  font-size: var(--font-size);
}

img {
  vertical-align: top;
}

.form-flexs {
  display: flex;
  position: relative;
}

.select, .input, .textarea {
  display: inline-block;
  height: 4rem;
  border: 0.1rem solid #DDD;
  background-color: #fff;
  padding: 0.5rem 1rem 0.6rem;
  font-size: 1.8rem;
  font-weight: 700;
  color: #333;
}
.select:focus, .input:focus, .textarea:focus {
  border-color: #666;
}
.select:disabled, .input:disabled, .textarea:disabled {
  background: #EEE;
  color: #999;
}
.select-block, .input-block, .textarea-block {
  display: block;
  width: 100%;
}

.select-wrap.input-block {
  display: block;
  width: 100%;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.input::-webkit-input-placeholder, .textarea::-webkit-input-placeholder {
  color: #333;
}
.input:-ms-input-placeholder, .textarea:-ms-input-placeholder {
  color: #333;
}
.input::placeholder, .textarea::placeholder {
  color: #333;
}
.input:-ms-input-placeholder, .textarea:-ms-input-placeholder {
  color: #333 !important;
}
.input::-ms-input-placeholder, .textarea::-ms-input-placeholder {
  color: #333;
}
.input:disabled::-webkit-input-placeholder, .textarea:disabled::-webkit-input-placeholder {
  color: #999;
}
.input:disabled:-ms-input-placeholder, .textarea:disabled:-ms-input-placeholder {
  color: #999;
}
.input:disabled::placeholder, .textarea:disabled::placeholder {
  color: #999;
}
.input:disabled:-ms-input-placeholder, .textarea:disabled:-ms-input-placeholder {
  color: #999 !important;
}
.input:disabled::-ms-input-placeholder, .textarea:disabled::-ms-input-placeholder {
  color: #999;
}

.textarea {
  height: 200px;
  overflow-y: auto;
  resize: none;
  line-height: 1.6;
  font-size: 1.6rem;
}
.textarea::-webkit-scrollbar {
  width: 0.6rem;
}
.textarea::-webkit-scrollbar-button {
  display: none;
}
.textarea::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 0.5rem;
}
.textarea::-webkit-scrollbar-thumb {
  background: transparent;
  border-radius: 0.5rem;
}

.form-checkbox {
  position: relative;
}
.form-checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0.01;
  overflow: hidden;
}
.form-checkbox label {
  display: inline-block;
  position: relative;
  vertical-align: middle;
}
.form-checkbox label:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 3.4rem;
  transform: translate(0, -50%);
  transition: all 0.15s;
  opacity: 0;
}
.form-checkbox label:after {
  content: "";
  position: absolute;
}
.form-checkbox .item-img {
  position: relative;
  z-index: 1;
}

.form-checkbox {
  overflow: hidden;
  padding-bottom: 2px;
}
.form-checkbox label {
  cursor: pointer;
  width: 100%;
  height: 100%;
}
.form-checkbox label.chk-blue:before {
  width: 1.8rem;
  height: 1.8rem;
  background: url(../img/ico-table1-check-blue.png) no-repeat 0 0/100% auto;
  z-index: 9;
}
.form-checkbox label.chk-yellow:before {
  width: 1.8rem;
  height: 1.9rem;
  background: url(../img/ico-table1-check-yellow.png) no-repeat 0 0/100% auto;
  z-index: 9;
}
.form-checkbox input:checked + label:before {
  opacity: 1 !important;
}
.sr_only, legend {
  position: absolute;
  z-index: -1;
  display: inline-block;
  overflow: hidden;
  height: 1px;
  width: 1px;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  word-break: initial;
  word-wrap: initial;
}

