.wrap-touch .touch {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
  padding-top: 2rem;
  border-top: 1px solid #282be8;
}
.wrap-touch .is-touch {
  position: relative;
  animation: slide 1s infinite alternate;
  animation-play-state: running; /* 기본 상태에서 애니메이션이 실행됨 */
}
.wrap-touch .is-touch.in, .wrap-touch .is-touch:focus {
  animation-play-state: paused; /* hover 시 애니메이션 멈춤 */
}
.wrap-touch .txtgroup {
  color: #fff;
  font-family: "SUIT";
  font-weight: 300;
  text-align: center;
  margin-top: 1rem;
  line-height: 1.7;
}
.wrap-touch .txtgroup p b {
  font-family: "SUIT";
  font-weight: 700;
}
.wrap-touch .txtgroup p:nth-child(1) {
  font-size: 1.8rem;
}
.wrap-touch .txtgroup p:nth-child(2) {
  font-size: 2rem;
}

@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(30px); /* 원하는 이동 거리 */
  }
}
